html {
  &.is-kuroigamen {
    background-color: #4da868;
    background-image: url("/images/kuroigamen/kuroigamen-bg.svg");
    background-repeat: repeat;
    background-size: 60px; } }

.kuroigamen-hero {
  display: block;
  @include media-breakpoint-down(sm) {
    display: block;
    height: auto; }
  .container {
    width: 100%; } }

.kuroigamen-hero__inner {
  @include position(relative);
  width: 100%; }

.kuroigamen-hero__site-title {
  display: flex;
  justify-content: center;
  @include margin(vertical, 4rem);
  @include media-breakpoint-down(sm) {
    @include margin(vertical, 1.5rem); } }

.kuroigamen-hero__site-title-link {
  display: block;
  width: 100%;
  max-width: 35rem;
  @include margin(horizontal, auto);
  &:hover {
    opacity: .8; }
  @include media-breakpoint-down(sm) {
    width: calc(100% - 2rem); } }

.kuroigamen-hero__site-title-link-image {
  width: 100%;
  height: auto;
  display: block;
  @include media-breakpoint-down(sm) {
    margin-bottom: 0;
    @include size(100% auto); } }



