a {
  transition: all .2s ease-in; }

.sm-container {
  max-width: 45rem;
  @include padding(horizontal, 1rem);
  @include margin(horizontal, auto); }

.md-container {
  max-width: 58rem;
  @include padding(horizontal, 1rem);
  @include margin(horizontal, auto); }

@font-face {
  font-family: "Kagi Kakko";
  font-weight: 100;
  src: local("Hiragino Sans"), local("Hiragino Kaku Gothic ProN"), local("Yu Gothic"), local("Meiryo");
  unicode-range: U+300C-300F; }

h1,
h2,
h3,
h4, {
  font-family: "Kagi Kakko", Hiragino Sans, sans-serif; }

.body.is-capture {
  background-color: white;
  min-height: 100vh;
  img {
    display: block;
    @include margin(horizontal, auto); } }



