.articles__header + .articles__items {
  margin-top: 2rem;
  @include media-breakpoint-down(sm) {
    margin-top: 1.5rem; } }

.articles__title {
  @include text-block(2rem 1, flex 700);
  justify-content: center;
  font-family: Roboto, sans-serif;
  @include media-breakpoint-down(sm) {
    font-size: 1.5rem; }
  &.is-kuroigamen {
    color: $reversal-text; } }



