.is-long-text {
  .ad {
    background-color: #edf0f0;
    border-radius: .25rem;
    @include margin(vertical, 2em); }

  .ad__header {
    padding: 1em 1.75em;
    border-bottom: 1px dashed #bdc5c5;
    *:first-child {
      margin-top: 0; }
    @include media-breakpoint-down(sm) {
      padding: .75em 1em; } }

  .ad__title {
    margin: 0;
    padding: 0;
    border: none;
    @include text-block(1.125rem 1.35);
    @include media-breakpoint-down(sm) {
      @include text-block(1.125rem 1.35); } }

  .ad__body {
    padding: 1.25em 1.75em;
    font-size: .875rem;
    @include media-breakpoint-down(sm) {
      padding: 1em; }
    *:first-child {
      margin-top: 0; }
    *:last-child {
      margin-bottom: 0; }
    p {
      margin-bottom: .6em;
      img:last-child {
        margin-bottom: 1em; } }
    img {
      border: none; } } }



